$brk-base-colors: ();
$brk-base-colors: map-merge((
				"brand-primary":      darken(adjust-hue(#2775FF, -39), 13.73),
				"secondary":          lighten(darken(adjust-hue(#7202bb, -109), 2.12), 6.08),
				"white":              #fff,
				"black":              #000,
				"brk-dark-base":      #272727,
				"brk-base-1":         darken(adjust-hue(#69adff, -39), 13.73),
				"brk-base-2":         darken(adjust-hue(#00c6ff, -39), 13.73),
				"brk-base-3":         darken(adjust-hue(#0F5AE0, -39), 13.73),
				"brk-base-4":         #e8ecff,
				"brk-base-5":         darken(adjust-hue(#00f6ff, -39), 13.73),
				"brk-base-6":         darken(adjust-hue(#0B98F6, -39), 13.73),
				"brk-base-7":         darken(adjust-hue(#a7ebff, -39), 13.73),
				"brk-base-8":         darken(adjust-hue(#1600ba, -39), 13.73),// TODO: !!!
				"brk-secondary-1":    lighten(darken(adjust-hue(#4f34c9, -109), 2.12), 6.08),
				"brk-secondary-2":    lighten(darken(adjust-hue(#4F00BA, -109), 2.12), 6.08),
				"brk-secondary-3":    lighten(darken(adjust-hue(#7400ba, -109), 2.12), 6.08),
				"brk-secondary-4":    lighten(darken(adjust-hue(#6e50c2, -109), 2.12), 6.08),
				"brk-secondary-5":    lighten(darken(adjust-hue(#6377DF, -109), 2.12), 6.08),
				"brk-secondary-6":    lighten(darken(adjust-hue(#b50aff, -109), 2.12), 6.08),
				"text-base-color-1":  #c4c4c4,
				"text-base-color-2":  darken(adjust-hue(#0F5AE0, -39), 13.73),
				"text-base-color-3":  #f3f3f3,
				"form-color-1":				#e8ecff
), $brk-base-colors);

@function safe-rgb($color) {
	@return unquote("#{red($color)}, #{green($color)}, #{blue($color)}");
}

/* ----------- Palette variable ----------- */
:root {
	@each $color, $value in $brk-base-colors {
		--#{$color}: #{$value};
	}

	// RGB
	@each $color, $value in $brk-base-colors {
		--#{$color}-rgb: #{safe-rgb($value)};
	}

	--b-radius: inherit;
}
/* ----------- Palette variable ----------- */
@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";

.brk-pagination {
  margin-top: 30px;

  .screen-reader-text {
    display: none;
  }

  .prev,
  .next {
    font-size: 0 !important;
  }

  .pagination {
    display: block;
  }

  // one
  &-one {

    .pagination {
      height: 64px;
      border-radius: 0;
      background-color: var(--white);
      box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12);

      @media (max-width:575px){
        height: 48px;
      }
    }

    .nav-links {
      font-weight: 600;
      display: flex;
      position: relative;
      width: 100%;
      padding-left: 22px;
      padding-right: 100px;

      @media (max-width:575px){
        padding-left: 10px;
        padding-right: 80px;
      }
    }

    .page-numbers {
      font-size: rem(14);
      line-height: 64px;
      position: relative;
      display: block;
      padding: 0 15px;

      @media (max-width:575px){
        padding: 0 10px;
        line-height: 48px;
      }

      &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 0;
        transition: all .3s ease-in-out;
        background-color: var(--brand-primary);
      }

      &:hover,
      &.current {
        color: var(--brand-primary);

        &:after {
          height: 3px;
        }
      }

      &.prev,
      &.next,
      &.dots {
        &:after {
          display: none;
        }
      }

      &.dots {
        padding: 0;
      }

      &.prev,
      &.next {
        position: absolute;
        width: 30px;
        height: 100%;
        font-family: $fa;

				[dir="rtl"] & {
					transform: rotate(180deg);
				}

        &:before {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          font-size: rem(24);
          font-weight: normal;
          transition: all .3s ease-in-out;
          color: var(--brand-primary);

					[dir="rtl"] & {
						top: calc(50% - 2px);
					}
        }

        &:hover {
          &:before {
            font-size: rem(28);
          }

        }

      }

      &.prev {
        right: 59px;

				[dir="rtl"] & {
					right: 19px;
				}

        @media (max-width:575px){
          right: 30px;

					[dir="rtl"] & {
						right: 0;
					}
        }

        &:before {
          content: '\f104';
          font-weight: 600;
        }
      }

      &.next {
        right: 19px;

				[dir="rtl"] & {
					right: 59px;
				}

        @media (max-width:575px){
          right: 0;

					[dir="rtl"] & {
						right: 30px;
					}
        }

        &:before {
          content: '\f105';
          font-weight: 600;
        }
      }
    }
  }

  // two
  &-two {

    .pagination {
      height: 58px;
      border-radius: 29px;
      overflow: hidden;
      background-color: var(--white);
      box-shadow: 0 6px 30px rgba(0, 0, 0, 0.12);
    }

    .nav-links {
      font-weight: 600;
      display: flex;
      position: relative;
      width: 100%;
    }

    .page-numbers {
      font-size: rem(14);
      line-height: 58px;
      position: relative;
      display: block;
      padding: 0 16px;
      color: #dfdfdf;

      &:hover {
        color: var(--brand-primary);
      }
      &.current {
        color: var(--brk-dark-base);
      }

      &.dots {
        display: none;
      }

      &.prev,
      &.next {
        width: 26px;
        padding: 0;
        height: 100%;
        font-family: $fa;

				[dir="rtl"] & {
					top: 2px;
					position: relative;
				}

        &:before {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          font-size: rem(24);
          font-weight: normal;
          transition: all .3s ease-in-out;
          color: var(--brand-primary);
        }

        &:hover {
          &:before {
            font-size: rem(28);
          }
        }
      }

      &.prev {
				[dir="rtl"] & {
					transform: rotate(180deg);
				}

        &:before {
          content: '\f104';
          font-weight: 600;
        }
      }

      &.next {
				[dir="rtl"] & {
					transform: rotate(180deg);
				}

        &:before {
          content: '\f105';
          font-weight: 600;
        }
      }
    }
  }

