        padding: 0;

        &:hover {
          color: var(--brk-dark-base);
        }

        &:after {
          display: none;
        }
      }

      &.prev,
      &.next {
        padding: 0;
        width: 52px;
        height: 52px;
        border-radius: 50%;
        font-family: $fa;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        border: 1px solid #ececec;

				[dir="rtl"] & {
					transform: translateY(-50%) rotate(180deg);
				}

        @media (max-width:575px){
          width: 34px;
          height: 34px;
          line-height: 32px;
        }

        &:before {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          font-size: rem(16);
          font-weight: normal;
          transition: all .3s ease-in-out;
          color: var(--brk-dark-base);
        }

        &:hover {
          border-color: var(--brand-primary);
          &:before {
            color: var(--white);
          }
        }
      }

      &.prev {
        left: -62px;

				[dir="rtl"] & {
					left: auto;
					right: -62px;
				}

        @media (max-width:575px){
          left: 1px;
        }

        &:before {
          content: '\f060';
          font-weight: 600;
        }
      }

      &.next {
        right: -62px;

				[dir="rtl"] & {
					right: auto;
					left: -62px;
				}

        @media (max-width:575px){
          right: 1px;
        }

        &:before {
          content: '\f061';
          font-weight: 600;
        }
      }
    }
  }

  // center
  &-center {
    display: flex;
    justify-content: center;

    .brk-prev-link,
    .brk-next-link {
      display: block;
    	width: 200px;
    	height: 54px;
    	border-radius: 27px;
      line-height: 54px;
      font-size: rem(16);
      font-weight: bold;
      text-transform: uppercase;
      margin: 0 5px;
      transition: all .3s ease-in-out;
      border: 1px solid #ececec;

      @media (max-width:575px){
        width: 130px;
        height: 40px;
        border-radius: 20px;
        line-height: 40px;
        font-size: rem(14);
      }

      &:hover {
        background-color: var(--brand-primary);
        border-color: var(--brand-primary);
        box-shadow: 0 5px 16px rgba(var(--brand-primary-rgb), 0.5);
        color: var(--white);
      }

      i {
        vertical-align: 0;
      }
    }

    .brk-prev-link {

      i {
        margin-right: 13px;

				[dir="rtl"] & {
					margin-right: 0;
					margin-left: 13px;
					transform: rotate(180deg);
					top: 2px;
					position: relative;
				}
      }
    }

    .brk-next-link {

      i {
        margin-left: 13px;
				
				[dir="rtl"] & {
					margin-left: 0;
					margin-right: 13px;
					transform: rotate(180deg);
					top: 2px;
					position: relative;
				}
      }
    }
  }

  // large
  &-large {
    display: flex;
    justify-content: space-between;
    position: relative;

    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 10px;
      right: 10px;
      transform: translateY(-50%);
      height: 1px;
      z-index: 1;
      background-color: #ececec;
    }

    .brk-prev-link,
    .brk-next-link {
      display: block;
    	width: 200px;
    	height: 54px;
    	border-radius: 27px;
      line-height: 54px;
      font-size: rem(16);
      font-weight: bold;
      text-transform: uppercase;
      margin: 0 5px;
      position: relative;
      transition: all .3s ease-in-out;
      z-index: 2;
      background-color: var(--white);
      border: 1px solid #ececec;

      &:hover {
        background-color: var(--brand-primary);
        border-color: var(--brand-primary);
        box-shadow: 0 5px 16px rgba(var(--brand-primary-rgb), 0.5);
        color: var(--white);
      }

      @media (max-width:575px){
        width: 130px;
        height: 40px;
        border-radius: 20px;
        line-height: 40px;
        font-size: rem(14);
      }

      i {
        vertical-align: 0;
      }
    }

    .brk-prev-link {

      i {
        margin-right: 13px;

				[dir="rtl"] & {
					margin-right: 0;
					margin-left: 13px;
					transform: rotate(180deg);
					top: 2px;
					position: relative;
				}
      }
    }

    .brk-next-link {

      i {
        margin-left: 13px;

				[dir="rtl"] & {
					margin-left: 0;
					margin-right: 13px;
					transform: rotate(180deg);
					top: 2px;
					position: relative;
				}
      }
    }
  }

}
